@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.DarkBackground {
  background: rgb(229, 238, 255);
}

.LightBackground {
  background: white;
}

.TaitLightBackground {
  background: rgb(229, 238, 255);
}

.TaitDarkBackground {
  background: rgb(0, 50, 210);
}

.card-body {
  padding: 0.5rem;
}

.read-more-button {
  padding-left: 0;
  padding-right: 0;
}

h1, h2, h3, h4, h5, h6, p {
  font-weight: 100;
  line-height: 1.6em;
}

body {
  font-family: 'Roboto Mono', 'monospace';
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.FontReadable {
  font-family: 'Roboto', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.FontStyleCode {
  font-family: 'Roboto Mono', monospace;
}

.FontColorBlack {
  color: #000000
}

.FontColorWhite {
  color: #FFFFFF
}

.FontColorDarkBlue {
  color: rgb(0, 50, 210);
}

.FontColorMediumGray {
  color: #616161
}

.ProjectTitleText {
  font-size: 14px;
  font-weight: bold;
  line-height: 150%;
  vertical-align: middle;
  font-family: "Roboto";
}

.SectionHeadingText {
  font-size: 20px;
  font-weight: 800;
}

.text-small {
  font-size: 10px;
}

